export const pageQuery = graphql`
  query($slug: String!){
  markdownRemark(frontmatter: {slug: {eq:  $slug}}) {
      frontmatter {
        slug
        title
        date
        keywords
        excerpt
      }
      html
    }
  }

`


export default post
